import React from 'react';
import Navbar from './Navbar';
import {makeStyles} from '@material-ui/styles'
import {
    Box,
    Grid,
    Card,
    CardActionArea,
    CardActions,
    CardContent,
    CardMedia,
    Typography,
    Button
} from '@material-ui/core';
import img1 from "../images/cityframe.jpg";
import img2 from "../images/scaryframe.jpg";
import img3 from "../images/greeting.png";
import img4 from "../images/shivratri1.jpg";


const useStyles = makeStyles({
    mainContainer:{
        background: "#223",
        height:"100%"
    },
    heading:{
        color: "tomato",
        padding: "3rem 0"
    },
    cardContainer:{
        maxWidth: 345,
        margin: "3rem",
        margin:"5rem auto"
    }
})

const VRFrames = () => {
    const classes = useStyles()
    return (
        <Box className = {classes.mainContainer}>
        <Navbar/>
        <Grid container justify="center" alignItems="center">
        <Typography variant="h5" align="center" className={classes.heading}>
        The computer-generated simulation of a three-dimensional image or environment that can be interacted with in a seemingly real or physical way by a person using special electronic equipment.
        </Typography>
            
            <Grid item xs={12} sm={8} md={6}>
            {/* Project 1 */}
            <Card className={classes.cardContainer}>
                <CardActionArea>
                    <CardMedia component="img" alt="project 1" height="180" image={img1}>

                    </CardMedia>
                    <CardContent>
                        <Typography gutterBottom variant="h5" >
                        The City
                            </Typography>
                        <Typography variant="body" color="textSecondary" component="p">
                    
                        This is a virtual reality frame demonstrating a city, In a scintillating manner.
                        
                        </Typography>
                    </CardContent>
                    <CardActions>
                        <Button size="small" color="primary">
                            Share
                        </Button>
                        <Button size="small" color="primary">
                            Live Demo
                        </Button>
                    </CardActions>
                </CardActionArea>
            </Card>
           </Grid>
            <Grid item xs={12} sm={8} md={6}>
           {/* Project 2 */}
           <Card className={classes.cardContainer}>
                <CardActionArea>
                    <CardMedia component="img" alt="project 1" height="180" image={img2}>

                    </CardMedia>
                    <CardContent>
                        <Typography gutterBottom variant="h5" >
                        The Scary City
                        </Typography>
                        <Typography variant="body" color="textSecondary" component="p">
                        
                        This is a virtual reality frame demonstrating a abandoned city, In a hilarious manner.
                        </Typography>
                    </CardContent>
                    <CardActions>
                        <Button size="small" color="primary">
                            Share
                        </Button>
                        <Button size="small" color="primary">
                            Live Demo
                        </Button>
                    </CardActions>
                </CardActionArea>
            </Card>
            </Grid>

            <Grid item xs={12} sm={8} md={6}>
           {/* Project 3 */}
           <Card className={classes.cardContainer}>
                <CardActionArea>
                    <CardMedia component="img" alt="project 1" height="180" image={img3}>

                    </CardMedia>
                    <CardContent>
                        <Typography gutterBottom variant="h5" >
                        Republic Day Greeting
                        </Typography>
                        <Typography variant="body" color="textSecondary" component="p">
                        
                        This is a very simple Virtual reality frame designed as a greeting for 69th Republic Day of India
                       
                        </Typography>
                    </CardContent>
                    <CardActions>
                        <Button size="small" color="primary">
                            Share
                        </Button>
                        <Button size="small" color="primary">
                            Live Demo
                        </Button>
                    </CardActions>
                </CardActionArea>
            </Card>
            </Grid>
            <Grid item xs={12} sm={8} md={6}>
       {/* Project 4 */}
       <Card className={classes.cardContainer}>
                <CardActionArea>
                    <CardMedia component="img" alt="project 1" height="180" image={img4}>

                    </CardMedia>
                    <CardContent>
                        <Typography gutterBottom variant="h5" >
                        Shiva Looks
                        </Typography>
                        <Typography variant="body" color="textSecondary" component="p">
                        This is a simple Virtual Reality frame consisting of all 12 jyotirling in the India.

                        </Typography>
                    </CardContent>
                    <CardActions>
                        <Button size="small" color="primary">
                            Share
                        </Button>
                        <Button size="small" color="primary">
                            Live Demo
                        </Button>
                    </CardActions>
                </CardActionArea>
            </Card>
            </Grid>
           </Grid>         
       </Box>
    )
}

export default VRFrames
