import React from 'react';
import {Typography, Avatar, Grid, Box} from '@material-ui/core';
import avatar from '../avatar.jpg'
import avatar2 from '../avatar2.jpg'
import {makeStyles} from '@material-ui/core/styles'
import Typed from 'react-typed'

const useStyles = makeStyles(theme=>({
    avatar:{
        width: theme.spacing(15),
        height: theme.spacing(15),
        margin: theme.spacing(1)
    } ,
    title:{
        color: "tomato"        
    },
    subtitle:{
        color: "tan"
    },
    typedContainer:{
        position: "absolute",
        top: "50%",
        left: "50%",
        transform  : "translate(-50%, -50%)",
        width: "100vw",
        textAlign: "center",
        zIndex: 1
    }
}))

const Header = () => {
    const classes = useStyles()
    return (
        <Box className={classes.typedContainer}>
            <Grid container justify="center">
            
            <Avatar className={classes.avatar} src={avatar} alt=""></Avatar>  
            
            </Grid>
            
            <Typography className={classes.title} varient = "h2">
                <h2><Typed strings={["Ankit Gautam"]} typeSpeed={40}/></h2>
            </Typography>
            
            <Typography className={classes.subtitle} varient = "h5">
                <h4><Typed strings = {["Full Stack Developer", "AR/VR Designer", "Infosion Hurray!!!!"]}
                    typeSpeed = {40}
                    backSpeed = {60}
                    loop
                /></h4>
                </Typography>         
        </Box>
    )
}

export default Header;