import React from 'react';
import {makeStyles} from '@material-ui/styles';
import {Typography, Box} from '@material-ui/core';
import Navbar from './Navbar';

const useStyles = makeStyles(theme => ({
    mainContainer:{
        background: "#223"
    },
    timeLine: {
        position: "relative",
        padding: '1rem',
        margin: '0 auto',
        "&:before": {
            content: "''",
            position: "absolute",
            height: "100%",
            border: "1px solid tan",
            right: "40px",
            top:0
        },
        "&:after": {
            content: "''",
            display: "table",
            clear: "both"
        }
    }, 
    timeLineItem:{
        padding: "1rem",
        borderBottom: "2px solid tan",
        position: "relative",
        margin: "1rem 3rem 1rem 1rem",
        clear: "both",
        "&:before":{
            content: "''",
            position: "absolute"
        },
        "&:after":{
            content: "''",
            position: "absolute",
            right: "-0.625rem",
            top: "calc(50% - 5px)",
            borderStyle: "solid",
            borderColor: "tomato tomato transparent transparent",
            borderWidth: "0.625rem",
            transform: "rotate(45deg)"
        }
    },
    timeLineYear:{
        textAlign: "center",
        maxWidth: "9.375rem",
        margin: "0 3rem 0 auto",
        fontSize: "1.8rem",
        background: "tomato",
        color: "white",
        lineHeight: 1,
        padding: "0.5rem 0 1rem",
        "&:before": {
            display: "none"
        }
    },
    heading:{
        color: "tomato",
        padding: "3rem 0",
        textTransform: "uppercase"
    },
    subHeading:{
        color: "white",
        padding: "0",
        textTransform: "uppercase"
    }
}));

const Resume = () => {

    const classes = useStyles()
    return (
        <>
            <Navbar/>
            <Box component="header" className={classes.mainContainer}>
                <Typography variant="h4" align="center" className={classes.heading}>
                    Work Experience 
                </Typography>
                <Box component="div" className={classes.timeLine}>
                    <Typography variant="h2" className={`${classes.timeLineYear} ${classes.timeLineItem}`}>2015-2016</Typography>
                    <Box component="div" className={classes.timeLineItem}>
                        <Typography variant="h5" className={classes.subHeading}>
                            Participated in Hackathons.
                        </Typography>
                        <Typography variant="body1" style={{color : "tomato"}}>
                            Participated in Smart India Hackathon 2017.
                        </Typography>
                        <Typography variant="subtitle1" style={{color : "tan"}}>
                            Lead my team in the finale, and got ourself in top 5 on that.
                        </Typography>
                    </Box>
                </Box>

                <Box component="div" className={classes.timeLine}>
                    <Typography variant="h2" className={`${classes.timeLineYear} ${classes.timeLineItem}`}>2016-2017</Typography>
                    <Box component="div" className={classes.timeLineItem}>
                        <Typography variant="h5"  className={classes.subHeading}>
                            Became Microsoft Student Partner.
                        </Typography>
                        <Typography variant="body1" style={{color : "tomato"}}>
                            Hosted some technical events
                        </Typography>
                        <Typography variant="subtitle1" style={{color : "tan"}}>
                            As a part of Microsoft Student Partner team, I got to organise some technical events such as
                            Hour of Code by Code.ORG in our campus as well as other school and colleges.
                        </Typography>
                    </Box>
                </Box>

                <Box component="div" className={classes.timeLine}>
                    <Typography variant="h2" className={`${classes.timeLineYear} ${classes.timeLineItem}`}>2017-2018</Typography>
                    <Box component="div" className={classes.timeLineItem}>
                        <Typography variant="h5"  className={classes.subHeading}>
                            Became Google Facilitator for MLCC Crash Course.
                        </Typography>
                        <Typography variant="body1"  style={{color : "tomato"}}>
                            I took charge to introduce Google Crowdsoource to my collegues from different colleges accross Indore.
                        </Typography>
                        <Typography variant="subtitle1" style={{color : "tan"}}>
                            I lead that team with small whatsapp group and then asked them to contribute data on that app as much as 
                            possible.
                        </Typography>
                    </Box>
                </Box>

                <Box component="div" className={classes.timeLine}>
                    <Typography variant="h2" className={`${classes.timeLineYear} ${classes.timeLineItem}`}>2018-2019</Typography>
                    <Box component="div" className={classes.timeLineItem}>
                        <Typography variant="h5" className={classes.subHeading}>
                            Get Set Go!!
                        </Typography>
                        <Typography variant="body1" style={{color : "tomato"}}>
                            Cracked the exam and interview for joining Infosys.
                        </Typography>
                        <Typography variant="subtitle1" style={{color : "tan"}}>
                            I enrolled in the Infosys state level drive and finally got my job in my dream company. Which
                            is dream companyfor many more.
                        </Typography>
                    </Box>
                </Box>


            </Box>
        </>
    )
}

export default Resume
