import React from 'react';
import Navbar from './Navbar';
import {makeStyles} from '@material-ui/styles'
import {
    Box,
    Grid,
    Card,
    CardActionArea,
    CardActions,
    CardContent,
    CardMedia,
    Typography,
    Button
} from '@material-ui/core';
import project1 from "../project1.jpg";


const useStyles = makeStyles({
    mainContainer:{
        background: "#223",
        height:"100%"
    },
    cardContainer:{
        maxWidth: 345,
        margin: "3rem",
        margin:"5rem auto"
    }
})

function Portfolio() {
    const classes = useStyles()
    return (
        <Box className = {classes.mainContainer}>
         <Navbar/>
         <Grid container justify="center" alignItems="center">
             <Grid item xs={12} sm={8} md={6}>
             {/* Project 1 */}
             <Card className={classes.cardContainer}>
                 <CardActionArea>
                     <CardMedia component="img" alt="project 1" height="140" image={project1}>

                     </CardMedia>
                     <CardContent>
                         <Typography gutterBottom variant="h5" >
                         AWC Digital Monitoring System
                             </Typography>
                         <Typography variant="body" color="textSecondary" component="p">
                     
                         I designed all the HTML pages and converted them to JSP pages, Basically done a work of front end developer.
                         
                         </Typography>
                     </CardContent>
                     <CardActions>
                         <Button size="small" color="primary">
                             Share
                         </Button>
                         <Button size="small" color="primary">
                             Live Demo
                         </Button>
                     </CardActions>
                 </CardActionArea>
             </Card>
            </Grid>
             <Grid item xs={12} sm={8} md={6}>
            {/* Project 2 */}
            <Card className={classes.cardContainer}>
                 <CardActionArea>
                     <CardMedia component="img" alt="project 1" height="140" image={project1}>

                     </CardMedia>
                     <CardContent>
                         <Typography gutterBottom variant="h5" >
                         E-learning Website
                         </Typography>
                         <Typography variant="body" color="textSecondary" component="p">
                         It was a mobile responsive web application in which admin can upload the learning assets and questions dynamically.
                         
                         </Typography>
                     </CardContent>
                     <CardActions>
                         <Button size="small" color="primary">
                             Share
                         </Button>
                         <Button size="small" color="primary">
                             Live Demo
                         </Button>
                     </CardActions>
                 </CardActionArea>
             </Card>
             </Grid>

             <Grid item xs={12} sm={8} md={6}>
            {/* Project 3 */}
            <Card className={classes.cardContainer}>
                 <CardActionArea>
                     <CardMedia component="img" alt="project 1" height="140" image={project1}>

                     </CardMedia>
                     <CardContent>
                         <Typography gutterBottom variant="h5" >
                         Technoquiz(A desktop application)
                         </Typography>
                         <Typography variant="body" color="textSecondary" component="p">
                         
                         As a practice of java core I created a desktop application where an individual can give tests and can see the scorecard.
                        
                         </Typography>
                     </CardContent>
                     <CardActions>
                         <Button size="small" color="primary">
                             Share
                         </Button>
                         <Button size="small" color="primary">
                             Live Demo
                         </Button>
                     </CardActions>
                 </CardActionArea>
             </Card>
             </Grid>
             <Grid item xs={12} sm={8} md={6}>
        {/* Project 4 */}
        <Card className={classes.cardContainer}>
                 <CardActionArea>
                     <CardMedia component="img" alt="project 1" height="140" image={project1}>

                     </CardMedia>
                     <CardContent>
                         <Typography gutterBottom variant="h5" >
                         Food Pantry System:
                         </Typography>
                         <Typography variant="body" color="textSecondary" component="p">
                         I created a web application where the outsider can register himself/herself and can find the rooms, food pantry for
himself/herself. Its was a great practice for JSP,JDBC and J2EE.

                         </Typography>
                     </CardContent>
                     <CardActions>
                         <Button size="small" color="primary">
                             Share
                         </Button>
                         <Button size="small" color="primary">
                             Live Demo
                         </Button>
                     </CardActions>
                 </CardActionArea>
             </Card>
             </Grid>
             {/* Project 5 */}
             <Grid item xs={12} sm={8} md={6}>
             <Card className={classes.cardContainer}>
                 <CardActionArea>
                     <CardMedia component="img" alt="project 1" height="140" image={project1}>

                     </CardMedia>
                     <CardContent>
                         <Typography gutterBottom variant="h5" >
                         Retail Management System
                         </Typography>
                         <Typography variant="body" color="textSecondary" component="p">
                         It was a team project where I was dealing with front end part and edited a template to create mobile responsive website.
                         </Typography>
                     </CardContent>
                     <CardActions>
                         <Button size="small" color="primary">
                             Share
                         </Button>
                         <Button size="small" color="primary">
                             Live Demo
                         </Button>
                     </CardActions>
                 </CardActionArea>
             </Card>
             </Grid>

            {/* Project 6 */}
            <Grid item xs={12} sm={8} md={6}>
             <Card className={classes.cardContainer}>
                 <CardActionArea>
                     <CardMedia component="img" alt="project 1" height="140" image={project1}>
                     
                     </CardMedia>
                     <CardContent>
                         <Typography gutterBottom variant="h5" >
                         VR designer
                         </Typography>
                         <Typography variant="body" color="textSecondary" component="p">
                         It was a project based on the virtual reality theme in which user can develop his/her house in virtual reality just by clicking
buttons. It was done by using Aframe framework which is nothing but the web framework.
                         </Typography>
                     </CardContent>
                     <CardActions>
                         <Button size="small" color="primary">
                             Share
                         </Button>
                         <Button size="small" color="primary">
                             Live Demo
                         </Button>
                     </CardActions>
                 </CardActionArea>
             </Card>
             </Grid>


            {/* Project 6 */}
            <Grid item xs={12} sm={8} md={6}>
             <Card className={classes.cardContainer}>
                 <CardActionArea>
                     <CardMedia component="img" alt="project 1" height="140" image={project1}>
                     </CardMedia>
                     <CardContent>
                         <Typography gutterBottom variant="h5" >
                         VR Corner
                         </Typography>
                         <Typography variant="body" color="textSecondary" component="p">
                         VR corner was a web application which was a centralized platform for all my VR frames I created and published on
Github.
                         </Typography>
                     </CardContent>
                     <CardActions>
                         <Button size="small" color="primary">
                             Share
                         </Button>
                         <Button size="small" color="primary">
                             Live Demo
                         </Button>
                     </CardActions>
                 </CardActionArea>
             </Card>
             </Grid>


            {/* Project 8 */}
            <Grid item xs={12} sm={8} md={6}>
             <Card className={classes.cardContainer}>
                 <CardActionArea>
                     <CardMedia component="img" alt="project 1" height="140" image={project1}>
                     
                     </CardMedia>
                     <CardContent>
                         <Typography gutterBottom variant="h5" >
                         Home Finder AR
                         </Typography>
                         <Typography variant="body" color="textSecondary" component="p">
                         I created a web application from which one can upload their details as well as a marker and using Augmented Reality for
detecting that marker Image and showing that information when the camera focuses on the marker.
                         </Typography>
                     </CardContent>
                     <CardActions>
                         <Button size="small" color="primary">
                             Share
                         </Button>
                         <Button size="small" color="primary">
                             Live Demo
                         </Button>
                     </CardActions>
                 </CardActionArea>
             </Card>
             </Grid>


            {/* Project 9 */}
            <Grid item xs={12} sm={8} md={6}>
             <Card className={classes.cardContainer}>
                 <CardActionArea>
                     <CardMedia component="img" alt="project 1" height="140" image={project1}>
                     
                     </CardMedia>
                     <CardContent>
                         <Typography gutterBottom variant="h5" >
                         AI Bot using MS Azure
                         </Typography>
                         <Typography variant="body" color="textSecondary" component="p">
                         I designed a chat bot using Microsoft Azure, I just need to put question and answers and I can link it with any of the
websites or web apps.
                         </Typography>
                     </CardContent>
                     <CardActions>
                         <Button size="small" color="primary">
                             Share
                         </Button>
                         <Button size="small" color="primary">
                             Live Demo
                         </Button>
                     </CardActions>
                 </CardActionArea>
             </Card>
             </Grid>


            {/* Project 10 */}
            <Grid item xs={12} sm={8} md={6}>
             <Card className={classes.cardContainer}>
                 <CardActionArea>
                     <CardMedia component="img" alt="project 1" height="140" image={project1}>
                     
                     </CardMedia>
                     <CardContent>
                         <Typography gutterBottom variant="h5" >
                         Mess Management(OnlineMess)
                         </Typography>
                         <Typography variant="body" color="textSecondary" component="p">
                         This was my project on HackIndore where I created a mobile responsive website and used python for data handling.
                         </Typography>
                     </CardContent>
                     <CardActions>
                         <Button size="small" color="primary">
                             Share
                         </Button>
                         <Button size="small" color="primary">
                             Live Demo
                         </Button>
                     </CardActions>
                 </CardActionArea>
             </Card>
             </Grid>


            </Grid>         
        </Box>
    )
}

export default Portfolio;

